.mobile-nav {
  width: 100%;
  height: 3%;
  position: relative;
}
.mobile-nav .logo {
  background-image: url("../Assets/Portfolio Logo LightMode.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 3.9rem;
  height: 3.9rem;
  position: absolute;
  top: 5%;
  left: 0;
}
.mobile-nav .logo.darkmode {
  background-image: url("../Assets/Portfolio Logo DarkMode.svg");
}
.mobile-nav .mobile-hamburger {
  width: 4rem;
  height: 4rem;
  outline: none;
  border: none;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  right: 0;
}
.mobile-nav .mobile-hamburger .mobile-icon {
  font-size: 2rem;
}
.mobile-nav .mobile-hamburger.darkmode {
  background-color: #3b4254;
  color: #ffffff;
}
.mobile-nav .mobile-menu {
  width: 100%;
  height: 70%;
  position: absolute;
  border-bottom: 2px solid #D8D8D8;
  top: 40%;
  z-index: 51;
  line-height: 2;
}
.mobile-nav .mobile-menu .mobile-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 42%;
  list-style: none;
}
.mobile-nav .mobile-menu .mobile-links .dark {
  margin-left: 10%;
}
.mobile-nav .mobile-menu-bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  background-color: #48b0c8;
}